import React, { Component } from "react";
import "../timeline.css";
import Day from "./Day"
import "bootstrap/dist/css/bootstrap.min.css";

class EmployeeName extends Component {
  constructor(props) {
    super(props);
  }

  render() {
      return(
        <th className="employee-name">
            {this.props.name}
        </th>
      )
  }
}

export default EmployeeName;
